import React, { useState, useEffect } from "react";
import MaterialTable from "material-table";
import { createTheme, Paper, ThemeProvider, Chip } from "@mui/material";
import { StylesProvider } from "@mui/styles";
import axios from "axios";
import { useParams } from "react-router-dom";

function DisableFieldEditable() {
  const params = useParams();
  const eventID = params.eventID;
  const [eventDevicesData, setEventDevicesData] = useState([]);

  useEffect(() => {
    // Generate a random number to act as a cache buster

    // Define the URL with query parameters, including the cache buster
    const apiUrl = `https://smart-donate.com/backend/api/getEvents.php?username=admin&password=aDMin1234&event_id=${eventID}`;

    // Make a GET request using Axios
    const fetchData = async () => {
      try {
        const response = await axios.get(apiUrl);
        // Handle the API response here
        // console.log("MAY DAAAATA:", response.data);

        setEventDevicesData(response?.data.devices);
      } catch (error) {
        // Handle any errors here
        console.error("Error:", error);
      }
    };
    const intervalId = setInterval(fetchData, 5000);

    // Clean up the interval when the component unmounts
    return () => clearInterval(intervalId);
  }, []);

  // useEffect(() => {
  //   const fetchData = async () => {
  //     try {
  //       const response = await axios.get(
  //         "https://cloud.ev-center.com/zakatseeb/be_light/api/getDevices.php",
  //         {
  //           params: {
  //             username: "admin",
  //             password: "aDMin1234",
  //             event_id: eventID,
  //           },
  //         }
  //       );

  //       if (response.data.length > 0) {
  //         // Handle the API response here
  //         const eventDevicesData = response.data;
  //         // console.log("Devices:", eventDevicesData);
  //         setEventDevicesData(eventDevicesData);
  //       }
  //     } catch (error) {
  //       // Handle any errors here
  //       console.error("Error:", error);
  //     }
  //   };

  //   fetchData();
  // }, [eventID]);

  const [Devices, setDevices] = useState([]);
  const [data, setData] = useState();

  useEffect(() => {
    if (eventDevicesData) {
      // Update Devices when eventDevicesData is available
      const updatedDevices = eventDevicesData.map((device) => ({
        id: device.id,
        name: device.name,
        place: device.place,
        donation_amount: parseFloat(device.donation_amount).toFixed(2),
        donation_count: device.donations_count,
        dimensions: device.dimensions,
        lastSeen: device.last_seen,
        status:
          device.online === 1 ? (
            <Chip
              label="متصل"
              variant="outlined"
              sx={{ color: "#01A66F", borderColor: "#01A66F", width: "80px" }}
            />
          ) : (
            <Chip
              label="غير متصل"
              variant="outlined"
              color="error"
              sx={{ width: "80px" }}
            />
          ),
        last_seen: new Date(device.last_seen).toLocaleString("en-US", {
          weekday: "short",
          month: "short",
          day: "2-digit",
          year: "numeric",
          hour: "2-digit",
          minute: "2-digit",
        }),
      }));

      setDevices(updatedDevices);
      setData(updatedDevices);
      // console.log("Devices:::::", updatedDevices);
    }
  }, [eventDevicesData]);

  useEffect(() => {
    setData(Devices);
  }, [Devices]);

  const [columns, setColumns] = useState([
    { title: "آخر ظهور", field: "last_seen", editable: "never" },
    { title: "حالة الجهاز", field: "status", editable: "never" },
    { title: "أبعاد الجهاز", field: "dimensions", editable: "never" },
    { title: "عدد المتبرعين", field: "donation_count", editable: "never" },
    {
      title: (
        <>
          <p style={{ padding: "0", margin: "0" }}>تم تحصيل</p>
          <p style={{ padding: "0", margin: "0" }}>(ر.ع)</p>
        </>
      ),
      field: "donation_amount",
      editable: "never",
    },
    { title: "الموقع", field: "place", editable: "always" },
    { title: "الاسم", field: "name", editable: "never" },

    { title: "الرمز التعريفي", field: "id", editable: "never" },
  ]);

  const tableTheme = createTheme();
  const [devicePlace, setDevicePlace] = useState();
  const updateDevicePlace = async (newData, index) => {
    try {
      const response = await axios.get(
        "http://cloud.ev-center.com/zakatseeb/be_light/api/editDevice.php",
        {
          params: {
            username: "admin",
            password: "aDMin1234",
            mac_address: eventDevicesData?.[index].mac_address,
            id: eventDevicesData?.[index]?.id,
            place: newData.place,
          },
        }
      );

      if (response.data.success) {
        // Device place updated successfully
        // console.log("Device place updated successfully");
      } else {
        // Handle API error
        console.error("API error:", response.data.error);
      }
    } catch (error) {
      // Handle network or other errors
      console.error("Error:", error);
    }
  };
  useEffect(() => {
    updateDevicePlace();
  }, [devicePlace]);

  return (
    <ThemeProvider theme={tableTheme}>
      <StylesProvider injectFirst>
        <Paper>
          <MaterialTable
            title="إضافة جهاز"
            columns={columns.map((col) => ({
              ...col,
              cellStyle: {
                textAlign: "center",
                fontSize: "12px",
                padding: "2px",
                //fontWeight: "bold",
              },
              headerStyle: {
                textAlign: "right",
                fontFamily: "Noto Kufi Arabic",
                fontWeight: "bold",
                padding: "2px",
              },
            }))}
            data={data}
            options={{
              paging: false, // Disable pagination
              search: false, // Disable search
              maxBodyHeight: "455px",
              toolbar: false,
              minBodyHeight: "455px",
              showTitle: false,
            }}
            localization={{
              header: {
                actions: "الخيارات",
              },
            }}
          />
        </Paper>
      </StylesProvider>
    </ThemeProvider>
  );
}

export default DisableFieldEditable;
