import React, { useState, useEffect } from "react";
import MaterialTable from "material-table";
import { createTheme, ThemeProvider, Paper, Chip } from "@mui/material";
import { StylesProvider } from "@mui/styles";
import axios from "axios";
import { useParams } from "react-router-dom";

function EditMaterialTable() {
  const params = useParams();
  const eventID = params.eventID;
  const [eventDevicesData, setEventDevicesData] = useState([]);

  useEffect(() => {
    // Generate a random number to act as a cache buster

    // Define the URL with query parameters, including the cache buster
    const apiUrl = `https://smart-donate.com/backend/api/getEvents.php?username=admin&password=aDMin1234&event_id=${eventID}`;

    // Make a GET request using Axios
    const fetchData = async () => {
      try {
        const response = await axios.get(apiUrl);
        // Handle the API response here
        // console.log("MAY DAAAATA:", response.data);

        setEventDevicesData(response?.data.devices);
      } catch (error) {
        // Handle any errors here
        console.error("Error:", error);
      }
    };

    const intervalId = setInterval(fetchData, 5000);

    // Clean up the interval when the component unmounts
    return () => clearInterval(intervalId);
  }, [eventID]);

  const [columns, setColumns] = useState([
    { title: "آخر ظهور", field: "last_seen", editable: "never" },
    { title: "حالة الجهاز", field: "status", editable: "never" },
    { title: "أبعاد الجهاز", field: "dimensions", editable: "never" },
    { title: "عدد المتبرعين", field: "donation_count", editable: "never" },
    {
      title: "تم تحصبل",
      field: "donation_amount",
      editable: "never",
    },
    { title: "الموقع", field: "place", editable: "always" },
    { title: "الاسم", field: "name", editable: "never" },
    { title: "الرمز", field: "id", editable: "never" },
  ]);
  const [data, setData] = useState([]);
  const statusRenderer = (rowData) => {
    return rowData.online === 1 ? "متصل" : "غير متصل";
  };

  useEffect(() => {
    if (eventDevicesData) {
      // Update data when eventDevicesData is available
      const updatedData = eventDevicesData?.map((device) => ({
        id: device.id,
        name: device.name,
        place: device.place,
        donation_amount: parseFloat(device.donation_amount).toFixed(2),
        donation_count: device.donations_count,
        dimensions: device.dimensions,
        status: statusRenderer(device), // Assign the JSX component here
        last_seen: new Date(device.last_seen).toLocaleString("en-US", {
          weekday: "short",
          day: "2-digit",
          month: "short",
          year: "numeric",
          hour: "2-digit",
          minute: "2-digit",
        }),
      }));

      setData(updatedData);
    }
  }, [eventDevicesData]);
  const tableTheme = createTheme();

  return (
    <ThemeProvider theme={tableTheme}>
      <StylesProvider injectFirst>
        <Paper>
          <MaterialTable
            title={" الأجهزة"}
            columns={columns.map((col) => ({
              ...col,
              cellStyle: {
                textAlign: "center",
                fontSize: "12px",
                padding: "5px",
                //fontWeight: "bold",
              },
              headerStyle: {
                textAlign: "right",
                padding: "5px",
                fontWeight: "bold",
                // position: "sticky", // Make header sticky
                // top: 0, // Stick to the top
                // zIndex: 1, // Add z-index to layer it above the scrollable content
                // backgroundColor: "#fff", // Add a background color if needed
                fontFamily: "Noto Kufi Arabic",
              },
            }))}
            data={data}
            editable={{
              onRowUpdate: (newData, oldData) =>
                new Promise((resolve, reject) => {
                  const apiUrl = `https://smart-donate.com/backend/api/editDevice.php?username=admin&password=aDMin1234&id=${newData.id}&place=${newData.place}`;

                  // Send a PUT request to your API to update the Place
                  axios
                    .put(apiUrl)
                    .then((response) => {
                      // Handle the API response here, for example, check if the update was successful
                      // console.log("API Response:", response.data);

                      // Update the table data with the newly edited row
                      setData((prevData) => {
                        const dataUpdate = [...prevData];
                        const index = oldData.tableData.id;
                        dataUpdate[index] = newData;
                        return dataUpdate;
                      });

                      // Resolve the promise to update the row if the API call was successful
                      resolve();
                    })
                    .catch((error) => {
                      // Handle any errors here
                      console.error("Error:", error);

                      // Reject the promise to keep the original row if the API call failed
                      reject();
                    });
                }),

              onRowDelete: (oldData) =>
                new Promise((resolve, reject) => {
                  const apiUrl = `https://smart-donate.com/backend/api/editDevice.php?username=admin&password=aDMin1234&id=${oldData.id}&event_id=0`;

                  // Send a DELETE request to your API
                  axios
                    .delete(apiUrl)
                    .then((response) => {
                      // Handle the API response here, for example, check if the delete was successful
                      // console.log("API Response:", response.data);

                      // Update the table data to remove the deleted row
                      const updatedData = [...data];
                      const index = oldData.tableData.id;
                      updatedData.splice(index, 1); // Remove the row at the specified index
                      setData(updatedData); // Update the table data

                      // Resolve the promise to delete the row if the API call was successful
                      resolve();
                    })
                    .catch((error) => {
                      // Handle any errors here
                      console.error("Error:", error);

                      // Reject the promise to keep the row if the API call failed
                      reject();
                    });
                }),
            }}
            options={{
              paging: false, // Disable pagination
              search: false, // Disable search
              showTitle: false,
              toolbar: false,
              header: true,
              maxBodyHeight: "460px",
              minBodyHeight: "460px",
              actionsCellStyle: {
                fontFamily: "Noto Kufi Arabic",
                color: "#BDD99E",
              },
            }}
            align="left"
            localization={{
              header: {
                actions: (
                  <p
                    style={{
                      fontFamily: "Noto Kufi Arabic",
                      fontWeight: "bold",
                    }}
                  >
                    الخيارات
                  </p>
                ),
              },
            }}
          />
        </Paper>
      </StylesProvider>
    </ThemeProvider>
  );
}

export default EditMaterialTable;
